@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,600;0,800;1,200;1,800&family=Open+Sans:wght@400;600;700&display=swap);
/*
font-family: 'Montserrat', sans-serif;
*/


.DuckHeader {
  width: 100%;
  padding-left: 1.5rem;
  padding-top: 0.5rem;
}

.TeamName {
  font-family: "Montserrat", sans-serif;
  font-weight: 800;
  font-size: 2.8rem;
  color: #ffbf00;
  float: left;
}

.MainMenu {
  width: 40%;
  padding-right: 3.7rem;
  font-family: "Open Sans", sans-serif;
  font-size: 1.2rem;
  float: right;
  display: flex;
  justify-content: space-between;
  padding-top: 3rem;
}

.MenuItem {
  color: #696969;
  text-decoration: none;
}

.MenuItem:hover {
  font-weight: 700;
}

.DuckImage {
  width: 100%;
  display: flex;
  justify-content: center;
}

